.card {
  background:green;
  border-radius: 2px;
  height: 149px;
  margin: 1rem;
  position: relative;
  width: 108px;
  -webkit-box-shadow: 0 3px 6px #999, 0 3px 6px #999;
          box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  
  text-align: left;
}

.card:hover {
  -webkit-box-shadow: 0 6px 12px #999, 0 6px 12px #999;
          box-shadow: 0 6px 12px #999, 0 6px 12px #999;
}

.card > .img-container {
  height: 100%;
  overflow: hidden;
  text-align: center;
}


.card > .content {
  padding-left: 1rem;
  padding-right: 1rem;
  font-size: 15px;
}









